.countdown {
    height: 100vh;
    position: relative;
    overflow: hidden;
}

.bgImg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
}

.imacVideo {
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.intro {
    text-align: center;
    padding: 0;
}

.intro h2 {
    font-size: 80px;
    font-weight: 1000;
    margin-bottom: 50px;
    color: white;
    font-family: 'Lobster Two', cursive;
    position: relative;
    text-transform: uppercase;
}

.countdownWrapper {
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
}

.countdownWrapper p {
    margin-right: 30px;
    color: white;
    font-weight: 1000;
    font-size: 80px;
    margin-bottom: 50px;
    font-family: 'Lobster Two', cursive;
    position: relative;
    text-transform: uppercase;
}

.countdownWrapper p:last-child {
    margin-right: 0;
}

.countdownWrapper span {
    font-size: 60px;
    display: block;
    font-family: 'Lobster Two', cursive;
    background: linear-gradient(78deg, #e10098 8%, #fe7351 99%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    background-clip: text;
    text-transform: uppercase;
}

.newYearText {
    font-size: 55px;
    font-weight: bold;
    color: #fff;
    text-align: center;
    animation: pop 1.2s ease-in-out infinite alternate;
}

@keyframes pop {
    from {
        transform: scale(1);
    }

    to {
        transform: scale(1.1);
    }
}

/* Responsive */
@media (max-width: 768px) {
    .intro h2 {
        font-size: 50px;
        margin-bottom: 30px;
    }

    .countdownWrapper p {
        font-size: 25px;
        margin-right: 10px;
    }

    .countdownWrapper span {
        font-size: 35px;
    }
}

@media (max-width: 480px) {
    .intro h2 {
        font-size: 40px;
    }

    .countdownWrapper p {
        font-size: 40px;
    }

    .countdownWrapper span {
        font-size: 25px;
    }
}